import { Error } from '../../Error';

type BrowserErrorProps = {
  onClick: () => void;
  header?: React.ReactNode;
  footer?: React.ReactNode;
};
export function BrowserError({
  onClick,
  header,
  footer,
}: BrowserErrorProps): JSX.Element {
  return (
    <Error
      text={`К сожалению, ${process.env.EXTENSION_TITLE} не поддерживает данный
      браузер. Вы можете продолжить пользоваться расширением, но его работа
      не гарантирована. Для получения списка поддерживаемых браузеров и
      другой информации о работе расширения перейдите на страницу`}
      textLink={
        <a href={process.env.HELP_URL} target="_blank" rel="noreferrer">
          FAQ
        </a>
      }
      buttonText="Ok"
      onClick={onClick}
      header={header}
      footer={footer} 
    />
  );
}
