import { fetchSubscriptionInfo } from '@api/premium/fetchPremiumData';
import { setLocalStoragePremiumData } from '@app/storage/local/premium';
import {
  LocalStorageKey,
  getLocalStorageData,
  setLocalStorageData,
} from '@app/storage/local/shared';
import { getSessionMetaApiData } from '@app/storage/session/metaApiData';
import { wait } from '@popup/shared/utils/wait';
import {
  useLayoutEffect,
  useRef,
  useState,
  type ChangeEvent,
  type FormEvent,
  type RefObject,
} from 'react';
import { useNavigate } from 'react-router-dom';
import { RoutePathType } from '../../../types/route';
import { getDeviceId, getDeviceIP } from '@background/client/utils';

type EnterIdFormHookValues = {
  enteredId: string;
  isError: boolean;
  isLoading: boolean;
  inputRef: RefObject<HTMLInputElement>;
  handleChange: (e: ChangeEvent<HTMLInputElement>) => void;
  handleSubmit: (e: FormEvent<HTMLFormElement>) => Promise<void>;
  clearError: () => void;
  isIdSettled: boolean;
};

export function useEnterIdForm<T extends Record<string, string>>({
  RoutePath,
}: {
  RoutePath: RoutePathType<T>;
}): EnterIdFormHookValues {
  const [enteredId, setEnteredId] = useState('');
  const [isError, setIsError] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [isIdSettled, setIsIdSettled] = useState(false);
  const inputRef = useRef<HTMLInputElement>(null);
  const navigate = useNavigate();

  useLayoutEffect(() => {
    inputRef.current?.focus();
  }, []);

  function handleChange(e: ChangeEvent<HTMLInputElement>): void {
    setEnteredId(e.target.value);
  }

  async function handleSubmit(e: FormEvent<HTMLFormElement>): Promise<void> {
    e.preventDefault();
    if (enteredId.trim() === '') {
      setIsError(true);
      return;
    }

    try {
      setIsError(false);
      setIsLoading(true);
      const userId = enteredId.trim();

      const [{ apiBaseUrl }, deviceId, deviceIp, { currentState }] =
        await Promise.all([
          getSessionMetaApiData(),
          getDeviceId(),
          getDeviceIP(),
          getLocalStorageData([LocalStorageKey.CurrentState]),
        ]);

      const [subscriptionInfo] = await Promise.all([
        fetchSubscriptionInfo(apiBaseUrl, {
          userId,
          deviceId,
          deviceIp,
        }),
        wait(1500),
      ]);

      if ('error' in subscriptionInfo) {
        if (subscriptionInfo.error === 'changeDevice') {
          await setLocalStorageData({
            targetState: 'disconnected',
          });

          await setLocalStoragePremiumData({
            userId,
          });

          navigate(RoutePath.ChangeDevice, { state: { userId } });
          return;
        }
        throw new Error(subscriptionInfo.error);
      }

      const { daysLeft, paidUntil, status, secondsLeft } = subscriptionInfo;
      await setLocalStoragePremiumData({
        userId,
        hasPremium: status === 'New' ? false : true,
        daysLeft,
        paidUntil,
        status,
        secondsLeft,
      });

      if (currentState === 'connected') {
        await wait(500);
      }
      setIsIdSettled(true);
    } catch (error) {
      console.warn(error);
      setIsError(true);
    } finally {
      setIsLoading(false);
    }
  }

  function clearError() {
    setIsError(false);
  }

  return {
    enteredId,
    isError,
    isLoading,
    isIdSettled,
    inputRef,
    handleChange,
    handleSubmit,
    clearError,
  };
}
