import { type CancelSubscriptionStatus } from '@api/premium/urls';
import {
  getSubscriptionInfo,
  setLocalStoragePremiumData,
  type ExtendedPaymentStatus,
} from '@app/storage/local/premium';
import { RoutePath } from '@popup/project/youboost-neo/routes/types/RoutePath';
import { useTheme } from '@popup/shared/contexts/Theme';
import { usePaymentStatus } from '@popup/shared/hooks/usePaymentStatus';
import { wait } from '@popup/shared/utils/wait';
import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { cancelSubscription } from '../api/cancelSubscription';
import { useLocalStorage } from '@popup/project/youboost-neo/hooks/useLocalStorage';
import { LocalStorageKey } from '@app/storage/local/shared';

type CancelPremiumHookValues = {
  handleCancelPremium: () => Promise<void>;
  isCancelPremiumLoading: boolean;
  isCancelPremiumError: boolean;
  isSuccess: boolean;
  status?: ExtendedPaymentStatus;
  cancelStatus: CancelSubscriptionStatus | undefined;
};

export function useCancelPremium(): CancelPremiumHookValues {
  const { setTheme } = useTheme();
  const navigate = useNavigate();
  const [isCancelPremiumLoading, setIsCancelPremiumLoading] = useState(false);
  const [isCancelPremiumError, setIsCancelPremiumError] = useState(false);
  const [isSuccess, setIsSuccess] = useState(false);
  const [cancelStatus, setCancelStatus] = useState<CancelSubscriptionStatus>();

  const [isUnfreeUser] = useLocalStorage(LocalStorageKey.IsUnfreeUser);

  const paymentStatus = usePaymentStatus();

  async function handleCancelPremium(): Promise<void> {
    try {
      setIsCancelPremiumLoading(true);

      if (paymentStatus === 'Paid') {
        const { status: cancelStatusResponse } = await cancelSubscription();
        setCancelStatus(cancelStatusResponse);
        setIsSuccess(true);
      }

      await getSubscriptionInfo();

      if (!isUnfreeUser) {
        setTheme('default');
        await Promise.all([
          setLocalStoragePremiumData({ hasPremium: false }),
          wait(1000),
        ]);
      }

      if (paymentStatus !== 'Paid') {
        navigate(RoutePath.Root);
      }
    } catch (error) {
      console.error(error);
      setIsCancelPremiumError(true);
    } finally {
      setIsCancelPremiumLoading(false);
    }
  }
  return {
    status: paymentStatus,
    isSuccess,
    handleCancelPremium,
    isCancelPremiumLoading,
    isCancelPremiumError,
    cancelStatus,
  };
}
