import { forwardRef, Fragment, useCallback } from 'react';
import clsx from 'clsx';
import { Button } from '@popup/shared/components/Button';
import { StepProps } from '../../types';
import styles from './PremiumStep.module.css';
import { WebIcon } from '@popup/shared/icons/WebIcon';
import { FourKIcon } from '@popup/shared/icons/FourKIcon';
import { YouTubeIcon } from '@popup/shared/icons/YouTubeIcon';
import { HeadphoneIcon } from '@popup/shared/icons/HeadphoneIcon';
import { ServerIcon } from '@popup/shared/icons/ServerIcon';
import { useHandlePremiumWithoutTelegram } from '../../hooks/useHandlePremiumWithoutTelegram';
import { Separator } from '../Separator/Separator';
import { XIcon } from '@popup/shared/icons/XIcon';
import { CloseButton } from '../CloseButton/CloseButton';

const CARDS = [
  {
    title: 'Доступ\nк любому сайту',
    image: WebIcon,
    size: 'big',
  },
  {
    title: 'Ролики\nв качестве 4К',
    image: FourKIcon,
    size: 'big',
  },
  {
    title: 'Отсутствие\nрекламы',
    image: YouTubeIcon,
    size: 'big',
  },
  {
    title: 'Быстрая\nподдержка',
    image: HeadphoneIcon,
    size: 'big',
  }
];

export const PremiumStep = forwardRef<HTMLDivElement, StepProps>(
  ({ onStepChange }, ref) => {
    const { handleClick, loading } = useHandlePremiumWithoutTelegram();

    const handleContinue = useCallback(() => {
      onStepChange(2);
    }, [onStepChange]);

    const handleClose = useCallback(() => {
      onStepChange(0);
    }, [onStepChange]);

    return (
      <div className={styles.container} ref={ref}>
        <div className={styles.content}>
          <div className={styles.title}>
            Юбуст Premium без ограничений
            <CloseButton onClick={handleClose} />
          </div>
          <div className={styles.cards}>
            {CARDS.map((card) => (
              <div
                className={clsx(
                  styles.card,
                  card.size === 'big' && styles.cardBig,
                  card.size === 'small' && styles.cardSmall,
                )}
                key={card.title}
              >
                <div className={styles.cardImage}>
                  <card.image />
                </div>
                <div className={styles.cardTitle}>
                  {card.title.split('\n').map((line, index) => (
                    <Fragment key={line}>
                      {line}
                      {index !== card.title.split('\n').length - 1 && <br />}
                    </Fragment>
                  ))}
                </div>
              </div>
            ))}
          </div>
        </div>
        <div className={styles.buttons}>
          <Button
            className={styles.button}
            variant="primary"
            onClick={handleClick}
            disabled={loading}
            loading={loading}
          >
            Продолжить с Premium
          </Button>
          <Separator />
          <Button
            className={styles.button}
            variant="secondary"
            onClick={handleContinue}
            disabled={loading}
          >
            Продолжить с ограничениями
          </Button>
        </div>
      </div>
    );
  },
);

PremiumStep.displayName = 'PremiumStep';
