import {
  LocalStorageKey,
  getLocalStorageData,
  setLocalStorageData,
} from '../../app/storage/local/shared';

function generateUUID(): string {
  // Generate a UUID, for example, using the uuid library if available
  // return 'xxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, (c) => {
  //   const r = (Math.random() * 16) | 0,
  //     v = c === 'x' ? r : (r & 0x3) | 0x8;
  //   return v.toString(16);
  // });
  return crypto.randomUUID();
}

async function generateDeviceId(): Promise<string> {
  const deviceId = generateUUID();
  await setLocalStorageData({ deviceId });
  return deviceId;
}

export async function getDeviceId(): Promise<string> {
  const { deviceId } = await getLocalStorageData(LocalStorageKey.DeviceId);
  if (deviceId) {
    return deviceId;
  }
  const newDeviceId = await generateDeviceId();
  return newDeviceId;
}

export async function getDeviceIP(): Promise<string | undefined> {
  try {
    const { deviceIp } = await getLocalStorageData(LocalStorageKey.DeviceIp);
    if (deviceIp) {
      return deviceIp;
    }

    const response = await fetch('https://api.ipify.org?format=json');
    const data = (await response.json()) as { ip: string };
    await setLocalStorageData({ deviceIp: data.ip });
    return data.ip;
  } catch (error) {
    console.warn('Error fetching public IP:', error);
  }
}
