import { clsx } from 'clsx';
import { forwardRef, type ButtonHTMLAttributes } from 'react';

import styles from './Button.module.css';
import { ButtonLoader } from './components/ButtonLoader/ui/ButtonLoader';

type ButtonProps = ButtonHTMLAttributes<HTMLButtonElement> & {
  variant?: 'primary' | 'primary-n' | 'secondary' | 'link';
  size?: 'default' | 'small';
  type?: 'button' | 'submit';
  sharp?: boolean;
  loading?: boolean;
};

export const Button = forwardRef<HTMLButtonElement, ButtonProps>(
  (
    {
      variant = 'primary',
      size = 'default',
      type = 'button',
      sharp = false,
      children,
      className,
      loading,
      ...rest
    },
    ref,
  ) => {
    return (
      <button
        ref={ref}
        className={clsx(
          styles.common,
          styles[variant],
          styles[size],
          sharp && styles.sharp,
          className,
        )}
        type={type === 'button' ? 'button' : 'submit'}
        {...rest}
      >
        {loading && <ButtonLoader />}
        {children}
      </button>
    );
  },
);

Button.displayName = 'Button';
