import { httpClient } from '../../../../../../../api/httpClient';
import {
  type CancelSubscriptionResponse,
  getCancelSubscriptionApiUrl,
} from '../../../../../../../api/premium/urls';
import {
  LocalStoragePremiumKey,
  getLocalStoragePremiumData,
} from '../../../../../../../app/storage/local/premium';
import {
  LocalStorageKey,
  getLocalStorageData,
} from '../../../../../../../app/storage/local/shared';
import { getSessionMetaApiData } from '../../../../../../../app/storage/session/metaApiData';

export async function cancelSubscription(): Promise<CancelSubscriptionResponse> {
  const { apiBaseUrl } = await getSessionMetaApiData();
  const [{ userId }, { deviceId, deviceIp }] = await Promise.all([
    getLocalStoragePremiumData(LocalStoragePremiumKey.UserId),
    getLocalStorageData([LocalStorageKey.DeviceId, LocalStorageKey.DeviceIp]),
  ]);

  const cancelSubscriptionApiUrl = getCancelSubscriptionApiUrl(apiBaseUrl);
  const data = await httpClient
    .post<CancelSubscriptionResponse>(cancelSubscriptionApiUrl, {
      json: {
        userId,
        deviceId,
        deviceIp,
      },
    })
    .json();
  return data;
}
