import {
  Feature,
  type FeatureList,
} from '@popup/shared/features/types/Feature';

import { fetchFeaturesData } from '@api/features';
import {
  getLocalStorageData,
  LocalStorageKey,
  setLocalStorageData,
} from '../local/shared';
import { formatResponse } from '@popup/shared/features/utils/formatResponse';

export async function getFeaturesData(): Promise<FeatureList> {
  const { featureList } = await getLocalStorageData(
    LocalStorageKey.FeatureList,
  );

  const data = await fetchFeaturesData();

  if (
    featureList !== undefined &&
    featureList?.[Feature.Version] === data[Feature.Version]
  ) {
    return featureList;
  }

  const formattedData = formatResponse(data);

  await setLocalStorageData({
    [LocalStorageKey.FeatureList]: formattedData,
  });

  return formattedData;
}
